:root {
  --main-background-color: #84a178;
  --main-text-color: #fcfcfc;
  --primary-text-color: #131313;
  --hover-text-color: #f8d24e;
  --background-hero-color: #2f303a;
}

p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

ul {
  margin: 0;
  padding-left: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

body {
  color: var(--main-text-color);
  background-color: var(--main-background-color);
  font-family: Montserrat, sans-serif;
}

.list {
  list-style: none;
}

.link {
  text-decoration: none;
}

ul {
  list-style: none;
}

address {
  font-style: normal;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.backdrop {
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, .2);
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
}

